#top {
  text-align: center;
  font-weight: bold;
  font-size: 3em;
  background-color: beige;
  color: blue;
  font-family: fantasy;
}

#imgText {
  text-align: center;
}

#img {
  width: 400;
  height: 150;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

#left {
  width: 85%;
  float: left;
  box-sizing: border-box;
}

#right {
  width: 15%;
  float: right;
  box-sizing: border-box;
}

#link {
  text-align: center;
  font-weight: bold;
  font-family: monospace;
  background-color: rgb(172, 197, 241);
  border-style: solid;
}

.signup,
.login,
.mypage,
.home,
.shop,
.insurance {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  color: inherit;
  text-decoration: none;
}

.signup,
.login,
.mypage,
.home {
  border-bottom: 1px solid black;
}
